import { memo, useState } from 'react';
import './AddToNews.css';

const AddtoNews = () => {
    const [popUp, setPopUp] = useState('');
    const [showPop, setShowPop] = useState(false);
    function PopUpShow(e) {
        e.preventDefault()
        if (popUp.length > 1) {
            setShowPop(true)
        }
        const timed = setTimeout(() => {
            setPopUp('');
            setShowPop(false);
        }, 5000)
        if (popUp === '' && showPop === false) {
            clearTimeout(timed)
        }

    }
    const stylePopUp = {
        opacity:showPop? 1 : 0,
        top: showPop ? "20px" : "0px",
        left:showPop? '0px': "-500px",
        transiiton: "0.5s all ease-in-out",
    }
    return (
        <>
            <div className='AddToNews bg-slate-900 relative overflow-x-hidden max-w-full'>
                <div className='container'>
                    <h1 className='text-white text-3xl text-center mb-2'>Subscribe In Our NewsLetter</h1>
                    <p className='text-white text-center text-sm'>you can add your Email in the next field to be up-to-date with new collections and brands</p>
                    <form onSubmit={PopUpShow}>
                        <div className='row flex-wrap items-center'>
                            <div className='input-item mt-10 m-auto'>
                                <input type='Email' placeholder='Type Your Email' value={popUp} onChange={(e) => setPopUp(e.target.value)} />
                            </div>
                            <div className='submit-item mt-10 m-auto'>
                                <button className='bg-red-600 block m-auto py-3 px-4 rounded-md text-white' type='submit'>Subscribe</button>
                            </div>
                        </div>
                    </form>
                    <div className='pop-up bg-slate-50 p-2 text-center absolute  rounded-md transition-all flex flex-row justify-center items-center' style={stylePopUp}>
                        <p className='text-green-600 text-sm'>Your Email {popUp} has Added Sucessfully !</p>
                    </div>
                </div>
            </div>
        </>
    )
}
export default memo(AddtoNews);